document.addEventListener("DOMContentLoaded", () => {
	// Includes
	//? // BurgerJS
class Burger {
	constructor(btn, list) {
		this.btn = btn;
		this.list = list;
		this.opened = false;
		this.btnDisabled = false;
		this.scrollWidth = window.innerWidth - document.body.clientWidth;

		this.btn.addEventListener('click', () => {
			this.toggle();
		})
	}

	open() {
		this.disableBtn();
		if (this.scrollWidth) {
			document.body.style.paddingRight = `${this.scrollWidth}px`
		}
		this.btn.classList.add('burger--active');
		this.btn.setAttribute('aria-expanded', true);
		this.list.classList.add('nav--active');
		this.list.setAttribute('aria-hidden', false);

		body.classList.add('lock')
	}

	close() {
		this.disableBtn();
		if (this.scrollWidth) {
			document.body.style.paddingRight = null;
		}
		body.classList.remove('lock')
		this.btn.classList.remove('burger--active');
		this.btn.setAttribute('aria-expanded', false);
		this.list.classList.remove('nav--active');
		this.list.setAttribute('aria-hidden', true);
	}

	toggle() {
		if (this.btn.classList.contains('burger--active')) {
			this.close();
		} else {
			this.open();
		}
	}

	disableBtn() {
		this.btn.disabled = true;
		this.list.addEventListener('transitionend', () => {
			this.btn.disabled = false;
		})
	}
};
	//? // Theme Switcher
const initialState = (themeName) => {
	localStorage.setItem('site-theme', themeName);
	document.documentElement.className = `html ${themeName}`;
}

if (localStorage.getItem('site-theme') === undefined || localStorage.getItem('site-theme') === null) {
	initialState('light-theme');
} else {
	initialState(localStorage.getItem('site-theme'));
}

const themeToggle = () => {
	if (localStorage.getItem('site-theme') === 'dark-theme') {
		initialState('light-theme');
	} else {
		initialState('dark-theme');
	}
}

// -- // ;
	// // Variables
const body = document.querySelector('.body');
const isMobileDevice = (/Android|webOS|iPhone|iPad|iPod|BlackBerry|IEMobile|Opera Mini/i.test(navigator.userAgent));
const isSmoothScrollSupported = 'scrollBehavior' in document.documentElement.style;
const myBurger = document.querySelector('.burger');
const header = document.querySelector('.header');
const nav = document.querySelector('.nav');
const burgerCl = new Burger(myBurger, nav);
const themeSwitcher = document.querySelector('.header__theme-switcher');;
	// -- //

	nav.addEventListener('click', (e) => {
		if (e.target.classList.contains('nav__overlay')) {
			burgerCl.close();
		}
	})
	window.addEventListener('scroll', () => {
		if (!header.classList.contains('header--active') && window.innerWidth < 900) {
			header.classList.add('header--active')
		} else if (!window.pageYOffset) {
			header.classList.remove('header--active');
		}

	})

	themeSwitcher.addEventListener('click', themeToggle);

})